.fl{float: left;}
.fr{float: right;}
input{
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
input:focus {
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 4px rgba(65,202,192,.6);
  border: 1px solid #41cac0;
  outline: 0;
}

